<template>
  <div id="app">
    <vue-slider v-bind:sendLeftActive="isLeftActive" v-bind:sendRightActive="isRightActive" @rightMove="testRight" @leftMove="testLeft"></vue-slider>
  </div>
</template>

<script>
import VueSlider from './components/VueSlider.vue'

export default {
  components: {
    'vue-slider': VueSlider,
  },
  data: function() {
    return {
      isLeftActive: true,
      isRightActive: false,
      stopVal: 0
    }
  },
  methods: {
    testRight: function() {
      this.stopVal += 1;
      this.isLeftActive = !this.isLeftActive;
      
      if (this.stopVal !== 0) this.isLeftActive = false
      // else if (this.stopVal === 5)

      if (this.stopVal === 5) this.isRightActive = true
    },
    testLeft: function() {
      this.stopVal -= 1;

      if (this.stopVal === 0) this.isLeftActive = true
      else {
        this.isRightActive = false
      }
    }
  }
}
</script>

